html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #041c2d;
  scroll-behavior: smooth;
}
html ::-webkit-scrollbar,
body ::-webkit-scrollbar {
  width: 0.4em;
  height: 0.4em;
}
html ::-webkit-scrollbar-thumb,
body ::-webkit-scrollbar-thumb {
  background-color: gold;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
}
html ::-webkit-scrollbar-track,
body ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #041c2d;
}
html *,
body * {
  box-sizing: border-box;
}
html h1,
body h1,
html h2,
body h2,
html h3,
body h3,
html h4,
body h4,
html h5,
body h5,
html h6,
body h6 {
  margin: 0;
}
html #root,
body #root {
  overflow-x: hidden;
  width: 100vw;
  display: flex;
  flex-direction: column;
}
html #root.mobile,
body #root.mobile {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
}
html #root.mobile .section,
body #root.mobile .section {
  font-size: 0.8rem !important;
}
html #root.mobile .section header,
body #root.mobile .section header {
  width: 100%;
  max-width: 100%;
}
html #root .navigation,
body #root .navigation {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: transparent;
  height: 0;
  transition: height 0.5s ease-out;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
html #root .navigation.sticky,
body #root .navigation.sticky {
  height: 5rem;
  background-color: #08324c;
  transition: height 0.3s ease-in;
}
html #root .navigation.sticky .header-buttons a.btn-primary,
body #root .navigation.sticky .header-buttons a.btn-primary {
  transform: translateY(0);
}
html #root .navigation nav,
body #root .navigation nav {
  max-width: 1700px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1em;
}
html #root .navigation nav .logo a,
body #root .navigation nav .logo a {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5em;
}
html #root .navigation nav .logo a .logo-icon,
body #root .navigation nav .logo a .logo-icon {
  width: 2rem;
  fill: gold;
}
html #root .navigation nav .logo a .logo-type,
body #root .navigation nav .logo a .logo-type {
  width: 8rem;
  fill: #fff;
}
html #root .navigation nav .header-buttons,
body #root .navigation nav .header-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4rem;
}
html #root .navigation nav .header-buttons a,
body #root .navigation nav .header-buttons a {
  transition: transform 0.5s ease-out;
}
html #root .navigation nav .header-buttons a.btn-primary,
body #root .navigation nav .header-buttons a.btn-primary {
  transform: translateY(-120px);
  transition: transform 0.3s ease-in;
}
.btn {
  color: #fff;
  border-radius: 0.2em;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0.8em 1rem;
  height: fit-content;
  font-weight: 600;
  cursor: pointer;
}
.btn:disabled {
  cursor: default;
}
.btn.btn-primary {
  background-color: gold;
  filter: brightness(90%);
}
.btn.btn-primary:hover,
.btn.btn-primary:focus {
  color: #fff !important;
  filter: brightness(100%);
}
.btn.btn-secondary {
  filter: brightness(90%);
}
.btn.btn-secondary:hover,
.btn.btn-secondary:focus {
  color: gold !important;
}
.btn.dark {
  background-color: #2f454f;
}
.btn.dark:hover,
.btn.dark:focus {
  filter: brightness(90%);
}
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.section {
  height: 100vh;
  display: flex;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 500;
  align-items: center;
  padding: 5em;
}
.section.center {
  justify-content: space-around;
  text-align: center;
}
.section.left {
  justify-content: left;
  text-align: left;
}
.section.left header {
  opacity: 0;
  transform: translateX(-110%);
}
.section.left header.slide {
  transform: translateX(0);
  opacity: 1;
}
.section.right {
  justify-content: right;
  text-align: right;
}
.section.right header {
  opacity: 0;
  transform: translateX(110%);
}
.section.right header.slide {
  transform: translateX(0);
  opacity: 1;
}
.section:nth-child(odd) {
  background-color: #08324c;
}
.section header {
  width: 800px;
  max-width: 90%;
  transition: transform 1s ease-in-out, opacity 1s ease-in;
}
.section header h1 {
  color: gold;
}
.section header .btn {
  font-size: 1rem;
}
.footer {
  height: fit-content !important;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 2em !important;
  width: 100%;
  padding: 3em;
  font-size: 1.1rem;
}
.footer .app-logo {
  text-align: center;
}
.footer .app-logo .logo {
  gap: 1em;
  display: flex;
}
.footer .app-logo .logo .logo-icon {
  width: 2rem;
  fill: gold;
  border-radius: 0.3em;
}
.footer .app-logo .logo .logo-type {
  width: 8rem;
  fill: #fff;
}
.footer .app-logo h5 {
  margin-top: 0.5em;
}
.footer .topic .title {
  margin: 0;
}
.footer .topic a {
  color: gold;
}
.footer .topic a span {
  display: flex;
  gap: 0.5em;
  align-items: center;
}
#Wizard {
  color: gold;
}
label.form-control {
  padding: 0.65em 1em;
}
label.form-control:active,
label.form-control:hover,
label.form-control:focus {
  cursor: pointer;
  filter: brightness(90%);
}
label.form-control,
input.form-control,
textarea.form-control,
button.form-control {
  border: 1px solid gold;
  display: flex;
  align-items: center;
  gap: 0.5em;
  width: 100%;
  filter: brightness(95%);
}
label.form-control[type="file"],
input.form-control[type="file"],
textarea.form-control[type="file"],
button.form-control[type="file"] {
  padding: 0.5em 1em;
}
label.form-control[type="file"]:active,
input.form-control[type="file"]:active,
textarea.form-control[type="file"]:active,
button.form-control[type="file"]:active,
label.form-control[type="file"]:hover,
input.form-control[type="file"]:hover,
textarea.form-control[type="file"]:hover,
button.form-control[type="file"]:hover,
label.form-control[type="file"]:focus,
input.form-control[type="file"]:focus,
textarea.form-control[type="file"]:focus,
button.form-control[type="file"]:focus {
  cursor: pointer;
  filter: brightness(90%);
}
.form-control {
  border-radius: 0.5em;
  padding: 0.8em 1em;
  outline: 0;
  border: 1px solid #eee;
}
.form-control.wide {
  width: 100%;
}
.form-control.invalid {
  border: 1px solid #f5222d !important;
}
.form-control.valid {
  border: 1px solid #52c41a !important;
}
.container {
  overflow-y: auto;
  overflow-x: hidden;
  height: 100%;
  scroll-behavior: smooth;
  z-index: 0;
  position: relative;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1em 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card {
  width: 800px;
  max-width: 90%;
  height: 100%;
  height: fit-content;
  margin: 0 auto;
  padding: 1em;
  background-color: #183960;
  color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card.wide {
  margin: 0 auto;
  width: 90%;
  max-width: 1200px;
}
.card .card-title {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}
.card .card-title svg.close {
  opacity: 0.6;
}
.card .card-title svg.close:active,
.card .card-title svg.close:hover,
.card .card-title svg.close:focus {
  cursor: pointer;
  opacity: 1;
}
